AGA,oBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CACA,iBAEA,CACA,sEACA,YACA,CACA,yEAEA,WAAA,CADA,UAEA,CACA,0EAIA,cAAA,CAFA,WAAA,CACA,gBAAA,CAFA,UAIA,CACA,oCAMA,UAAA,CALA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAEA,kBACA,CAKA,0EAFA,kBAAA,CADA,YAaA,CAVA,uCAMA,UAAA,CALA,mBAAA,CAGA,oBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAEA,iBAGA,CACA,2CACA,gBACA,CACA,yDAKA,wBAAA,CADA,iBAAA,CADA,oBAAA,CADA,UAAA,CAIA,gBAAA,CALA,SAMA,CACA,oDAQA,aAAA,CAPA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAIA,CACA,oCACA,eAAA,CACA,iBACA,CACA,yCAKA,qCAAA,CAJA,2CAAA,CAEA,uBAAA,CADA,2BAAA,CAEA,oBAAA,CAEA,8BAAA,CACA,uBAAA,CAQA,aAAA,CAPA,cAAA,CACA,aAAA,CAGA,eAAA,CACA,eAAA,CAGA,WAAA,CAFA,eAAA,CAHA,sBAAA,CADA,UAOA,CACA,+CACA,+BACA,CACA,6CACA,qCACA,CACA,wCACA,qBAAA,CACA,8BAAA,CACA,uBAAA,CASA,aAAA,CAPA,cAAA,CACA,aAAA,CAGA,eAAA,CACA,eAAA,CAKA,MAAA,CAJA,eAAA,CAPA,kBAAA,CAIA,sBAAA,CAKA,iBAAA,CACA,QAAA,CAPA,UASA,CACA,6CACA,aAAA,CAEA,WAAA,CADA,UAEA,CACA,yCACA,mCACA,cACA,CACA,oDAEA,cAAA,CADA,UAEA,CACA,oCAEA,aAAA,CACA,eAAA,CAFA,cAGA,CACA,CACA,yCACA,2BACA,UACA,CACA","sourcesContent":["<template>\n    <div class=\"wed-host-section\">\n        <div class=\"wed-host-section-container\" :class=\"{ loading: loading }\">\n            <div class=\"task-title wed-host-section-container-title\">\n                <button v-if=\"!editing\" @click=\"doEdit(true)\">\n                    <span>+</span> Add new task\n                </button>\n            </div>\n            <div class=\"d-flex justify-content-around\">\n                <div class=\"my-3\" v-if=\"members.length > 0\">\n                    <button\n                        v-if=\"editing\"\n                        @click=\"doEdit(false)\"\n                        class=\"btn btn-sm btn-cancel\"\n                        title=\"cancel task form\"\n                    >\n                        Cancel\n                    </button>\n                    <!-- <button\n                        v-else\n                        @click=\"doEdit(true)\"\n                        class=\"btn btn-sm wed-btn-main\"\n                        title=\"add new task\"\n                    >\n                        + add Tasks\n                    </button> -->\n                </div>\n            </div>\n            <div\n                v-if=\"success\"\n                class=\"alert alert-success alert-dismissible fade show mt-3\"\n                role=\"alert\"\n            >\n                Task Saved !\n                <button\n                    type=\"button\"\n                    class=\"close\"\n                    data-dismiss=\"alert\"\n                    aria-label=\"Close\"\n                >\n                    <span aria-hidden=\"true\">&times;</span>\n                </button>\n            </div>\n            <div class=\"wed-host-section-container-form\" v-if=\"editing\">\n                <div class=\"alert alert-danger\" role=\"alert\" v-if=\"hasError\">\n                    All fields are required!\n                </div>\n\n                <form class=\"form-row align-items-center\">\n                    <div class=\"form-group-custom\">\n                        <label for=\"title\" class=\"form-label\">New Task</label>\n                        <input\n                            @keyup.enter=\"saveTask\"\n                            type=\"text\"\n                            name=\"title\"\n                            id=\"title\"\n                            class=\"form-control\"\n                            v-model=\"fields.title\"\n                            placeholder=\"Add an new Task\"\n                        />\n                        <div v-if=\"errors && errors.title\" class=\"text-danger\">\n                            {{ errors.title[0] }}\n                        </div>\n                    </div>\n                    <div class=\"form-group-custom\">\n                        <label class=\"form-label\">Task Category</label>\n                        <select\n                            name=\"category_id\"\n                            class=\"form-control\"\n                            v-model=\"fields.category_id\"\n                        >\n                            <option\n                                v-for=\"(category, id) in categories\"\n                                :key=\"id\"\n                                :value=\"category.id\"\n                            >\n                                {{ category.name }}\n                            </option>\n                        </select>\n                    </div>\n                    <div class=\"form-group-custom\">\n                        <label class=\"form-label\">Assigned To</label>\n                        <div class=\"custom-select-box\">\n                            <span @click.stop=\"showDropdown = !showDropdown\">{{\n                                selectedMember?.name\n                            }}</span>\n                            <div v-if=\"showDropdown\">\n                                <span\n                                    v-for=\"(mem, id) in members\"\n                                    :key=\"id\"\n                                    :value=\"mem.id\"\n                                    @click=\"\n                                        showDropdown = false;\n                                        selectedMember = mem;\n                                        fields.member_id = mem.id;\n                                    \"\n                                    >{{ mem.name }}\n                                </span>\n                            </div>\n                        </div>\n                        <!-- <select\n                            name=\"member_id\"\n                            class=\"form-control\"\n                            v-model=\"fields.member_id\"\n                        >\n                            <option\n                                v-for=\"(mem, id) in members\"\n                                :key=\"id\"\n                                :value=\"mem.id\"\n                            >\n                                {{ mem.name }}\n                            </option>\n                        </select> -->\n                    </div>\n                    <div\n                        class=\"form-group-custom\"\n                        style=\"margin-bottom: 10px; margin-top: 12px\"\n                    >\n                        <label class=\"form-label\">Task Priority</label>\n                        <br />\n                        <label class=\"form-label-radio\">\n                            <input\n                                type=\"radio\"\n                                class=\"form-radio\"\n                                name=\"taskPriority\"\n                                v-model=\"fields.taskPriority\"\n                                value=\"low\"\n                            />Low</label\n                        >\n                        <label class=\"form-label-radio\">\n                            <input\n                                type=\"radio\"\n                                class=\"form-radio\"\n                                v-model=\"fields.taskPriority\"\n                                value=\"medium\"\n                            />Medium</label\n                        >\n                        <label class=\"form-label-radio\">\n                            <input\n                                type=\"radio\"\n                                class=\"form-radio\"\n                                v-model=\"fields.taskPriority\"\n                                value=\"high\"\n                            />High</label\n                        >\n                    </div>\n                    <div class=\"date-grp\">\n                        <div class=\"form-group-custom\">\n                            <input\n                                type=\"text\"\n                                name=\"startDate\"\n                                :min=\"minDate\"\n                                v-model=\"fields.startDate\"\n                                onfocus=\"(this.type='date')\"\n                                class=\"form-date form-control\"\n                                placeholder=\"Task start date\"\n                            />\n                            <div\n                                v-if=\"errors && errors.startDate\"\n                                class=\"text-danger\"\n                            >\n                                {{ errors.startDate[0] }}\n                            </div>\n                        </div>\n                        <div class=\"form-group-custom\">\n                            <input\n                                type=\"text\"\n                                name=\"completionDate\"\n                                v-model=\"fields.completionDate\"\n                                onfocus=\"(this.type='date')\"\n                                class=\"form-date form-control\"\n                                placeholder=\"Task end date\"\n                            />\n                            <div\n                                v-if=\"errors && errors.completionDate\"\n                                class=\"text-danger\"\n                            >\n                                {{ errors.completionDate[0] }}\n                            </div>\n                        </div>\n                        <div class=\"form-group-custom\">\n                            <input\n                                type=\"text\"\n                                name=\"startTime\"\n                                v-model=\"fields.startTime\"\n                                onfocus=\"(this.type='time')\"\n                                placeholder=\"Task start time\"\n                                class=\"form-date form-control\"\n                            />\n                            <div\n                                v-if=\"errors && errors.startTime\"\n                                class=\"text-danger\"\n                            >\n                                {{ errors.startTime[0] }}\n                            </div>\n                        </div>\n                        <div class=\"form-group-custom\">\n                            <input\n                                type=\"text\"\n                                name=\"completionTime\"\n                                v-model=\"fields.completionTime\"\n                                onfocus=\"(this.type='time')\"\n                                class=\"form-date form-control\"\n                                placeholder=\"Task end time\"\n                            />\n                            <div\n                                v-if=\"errors && errors.completionTime\"\n                                class=\"text-danger\"\n                            >\n                                {{ errors.completionTime[0] }}\n                            </div>\n                        </div>\n                    </div>\n                    <div class=\"form-group-custom ml-5\">\n                        <button\n                            v-if=\"editForm == false\"\n                            @click.prevent=\"addNewTask\"\n                            class=\"btn wed-btn-main btn-sm\"\n                        >\n                            Submit\n                        </button>\n                        <button\n                            v-if=\"editForm == true\"\n                            @click.prevent=\"updateTask\"\n                            class=\"btn wed-btn-main btn-sm\"\n                        >\n                            Update\n                        </button>\n                    </div>\n                </form>\n            </div>\n            <div class=\"row mt-3\" v-if=\"tasks.length > 0\">\n                <div\n                    v-for=\"task in reversedTasks\"\n                    :key=\"task.id\"\n                    class=\"task-list-single\"\n                >\n                    <div class=\"single-first-row\">\n                        <div>\n                            <span>{{ task.category.name }}</span>\n                            <span\n                                >Assigned to :\n                                <span style=\"font-weight: 900\">{{\n                                    task.assignable.name\n                                }}</span></span\n                            >\n                        </div>\n                        <div>\n                            <span>\n                                <img src=\"/assets/tag.svg\" alt=\"\" />\n                                {{ task.taskPriority }}\n                            </span>\n                            <span>\n                                <img\n                                    @click=\"setVal(task)\"\n                                    src=\"/assets/edit.svg\"\n                                    alt=\"\"\n                                />\n                                <img\n                                    @click=\"deleteTask(task)\"\n                                    src=\"/assets/trash.svg\"\n                                    alt=\"\"\n                                />\n                            </span>\n                        </div>\n                    </div>\n                    <div class=\"single-second-row\">\n                        {{ task.title }}\n                    </div>\n                    <div class=\"single-third-row\">\n                        <div>\n                            <img src=\"/assets/icon-calendar.svg\" alt=\"\" />\n                            <span>{{ task.startDate }}&nbsp&nbsp </span> ---\n                            <span>&nbsp&nbsp{{ task.completionDate }}</span>\n                        </div>\n                        <div>\n                            <img src=\"/assets/icon-clock.svg\" alt=\"\" />\n                            <span>{{ task.startTime }}</span> -\n                            <span>{{ task.completionTime }}</span>\n                        </div>\n                        <div>\n                            <span></span>\n                            {{ task.taskStatus }}\n                        </div>\n                    </div>\n                </div>\n            </div>\n\n            <div v-else>\n                <p>\n                    Nice job! You'v finished all your your assigned Tasks! add\n                    Tasks\n                </p>\n            </div>\n            <div>\n                <p class=\"wed-host-section-container-title\">Tasklist</p>\n                <p class=\"wed-host-section-container-title\">\n                    {{ invitation.brideName }} weds\n                    {{ invitation.groomName }} on {{ invitation.startDate }}\n                </p>\n            </div>\n            <div class=\"d-flex\">\n                <div style=\"width: 20%\">\n                    <ol>\n                        <li\n                            v-for=\"cat in taskCat\"\n                            :key=\"cat.id\"\n                            @click=\"filterTask(cat)\"\n                        >\n                            {{ cat.name }}\n                            <!-- <ul style=\"list-style: none\">\n                                                    <li v-for=\"child in cat.children\" :key=\"child\">\n                                                        {{child.name}}\n                                                    </li>\n                                                </ul> -->\n                        </li>\n                    </ol>\n                </div>\n                <div style=\"width: 80%\">\n                    <table>\n                        <tr\n                            v-for=\"wedtasks in filteredTasks\"\n                            :key=\"wedtasks.id\"\n                        >\n                            <td>\n                                <input type=\"checkbox\" class=\"text-danger\" />\n                                <label for=\"taskStatus\" class=\"form-label\"\n                                    ><i class=\"text-danger\">{{\n                                        wedtasks.name\n                                    }}</i></label\n                                >\n                            </td>\n                            <td>{{ wedtasks.pivot.timeline }} Days</td>\n                            <td>{{ wedtasks.pivot.taskStatus }}</td>\n                            <td>\n                                {{\n                                    wedtasks.pivot.notStarted == null\n                                        ? \"member to update task starting date\"\n                                        : \"member yet not started tasks\"\n                                }}\n                            </td>\n                            <td>\n                                {{\n                                    wedtasks.pivot.started !== null\n                                        ? \"memeber to update starting date\"\n                                        : \" member yet not started task\"\n                                }}\n                            </td>\n                            <td>{{ wedtasks.pivot.completed }}</td>\n                            <td>\n                                {{\n                                    wedtasks.pivot.member_id == null\n                                        ? \"task yet to be assigned\"\n                                        : \"task assigned to member and display his name\"\n                                }}\n                            </td>\n                        </tr>\n                    </table>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"hostId\", \"invitation\", \"tasksall\", \"taskList\", \"taskCat\"],\n    data() {\n        return {\n            editing: false,\n            loading: true,\n            selectedMember: null,\n            fields: {\n                // title:\"\",\n                invitation_id: this.invitation.id,\n                // pinned: false,\n                taskPriority: \"low\",\n                // taskStatus: \"notStarted\",\n                // member_id: \"\",\n                // category_id: \"\",\n            },\n            taskId: \"\",\n            taskTitle: \"\",\n            taskCategory: \"\",\n            taskMember: \"\",\n            taskPriority: \"\",\n            taskDueDate: \"\",\n            taskStatus: \"\",\n            errors: {},\n            success: false,\n            loaded: true,\n            hasError: false,\n            tasks: this.tasksall,\n            members: [],\n            categories: [],\n            image_srcs: \"/assets/frame881.png\",\n            editForm: false,\n            showDropdown: false,\n            filteredTasks: this.taskList,\n        };\n    },\n    mounted() {\n        this.getMembers();\n        this.getCategories();\n        // this.getTasks();\n    },\n    computed: {\n        reversedTasks() {\n            return [...this.tasks].reverse().sort();\n        },\n        minDate() {\n            var today = new Date();\n            var dd = today.getDate();\n            var mm = today.getMonth() + 1; //January is 0 so need to add 1 to make it 1!\n            var yyyy = today.getFullYear();\n            if (dd < 10) {\n                dd = \"0\" + dd;\n            }\n            if (mm < 10) {\n                mm = \"0\" + mm;\n            }\n\n            today = yyyy + \"-\" + mm + \"-\" + dd;\n            return today;\n        },\n    },\n    methods: {\n        filterTask(cat) {\n            this.filteredTasks = this.taskList.filter(\n                (obj) => obj.parent_id == cat.id\n            );\n        },\n        setMember(mem) {\n            console.log(\"heloooooooooooooooo\");\n            this.selectedMember = mem;\n        },\n        saveTask() {\n            this.tasks.push({\n                id: this.tasks.length + 1,\n                title: this.fields.title,\n                taskPriority: this.fields.taskPriority,\n                taskStatus: this.fields.taskStatus,\n                dueDate: this.fields.dueDate,\n                pinned: this.fields.pinned,\n                member_id: this.fields.member_id,\n                category_id: this.fields.category_id,\n            });\n            this.fields.title = \"\";\n            this.fields.taskPriority = \"low\";\n            this.fields.taskStatus = \"\";\n            this.fields.dueDate = \"\";\n            this.fields.member_id - \"\";\n            this.fields.category_id = \"\";\n            this.selectedMember = null;\n        },\n        doEdit(editing) {\n            this.editing = editing;\n            this.fields.title = \"\";\n            this.fields.taskPriority = \"low\";\n            this.fields.taskStatus = \"\";\n            this.fields.startDate = \"\";\n            this.fields.completionDate = \"\";\n            this.fields.startTime = \"\";\n            this.fields.completionTime = \"\";\n            this.fields.invitation_id = this.invitation.id;\n            (this.member_id = {}), (this.fields.category_id = \"\");\n            this.editForm = false;\n            this.taskId = null;\n            this.selectedMember = null;\n        },\n        togglePinned(task) {\n            task.pinned = !task.pinned;\n        },\n\n        addNewTask() {\n            var _this = this;\n            if (this.loaded) {\n                this.loaded = false;\n                this.success = false;\n                this.errors = {};\n                const taskStatus = \"Not Started\";\n                let type = \"member\";\n                if (this.selectedMember?.roles_user) {\n                    type = \"host\";\n                }\n                this.fields = { ...this.fields, taskStatus };\n                this.fields = { ...this.fields, type };\n                axios\n                    .post(\"/submittask\", this.fields)\n                    .then((response) => {\n                        _this.getTasks();\n                        _this.loading = false;\n                        _this.success = true;\n                        _this.fields = {\n                            invitation_id: this.invitation.id,\n                            taskPriority: \"low\",\n                        }; //Clear input fields.\n                        _this.selectedMember = null;\n                        _this.loaded = true;\n                        _this.success = true;\n                        _this.editing = false;\n                    })\n                    .catch((error) => {\n                        _this.loaded = true;\n                        if (error.response.status === 422) {\n                            _this.errors = error.response.data.errors || {};\n                        }\n                    });\n            }\n        },\n        getMembers() {\n            var _this = this;\n            axios\n                .get(\"/getmembers\")\n                .then(function (response) {\n                    _this.members = response.data;\n                })\n                .catch((error) => {\n                    console.log(\"Get All: \" + error);\n                });\n        },\n        getCategories: function () {\n            var _this = this;\n            axios\n                .get(route(\"admin.tasks.index\"))\n                .then((response) => {\n                    _this.categories = response.data.data;\n                    console.log(_this.categories);\n                })\n                .catch(function (error) {\n                    console.log(error);\n                });\n        },\n        getTasks() {\n            var _this = this;\n            axios\n                .get(\"/gettasks\")\n                .then(function (response) {\n                    _this.tasks = response.data;\n                    console.log(_this.tasks);\n                    _this.loading = false;\n                })\n                .catch((error) => {\n                    console.log(\"Get All: \" + error);\n                });\n        },\n        setVal(task) {\n            this.doEdit(true);\n            this.taskId = task.id;\n            this.fields.title = task.title;\n            this.fields.category_id = task.category_id;\n            this.fields.taskPriority = task.taskPriority;\n            this.fields.assignable_id = task.assignable_id;\n            this.fields.assignable_type = task.assignable_type;\n            this.fields.startDate = new Date(task.startDate)\n                .toISOString()\n                .split(\"T\")[0];\n            this.fields.completionDate = new Date(task.completionDate)\n                .toISOString()\n                .split(\"T\")[0];\n            this.fields.startTime = task.startTime;\n            this.fields.completionTime = task.completionTime;\n            this.fields.taskStatus = task.taskStatus;\n            this.editForm = true;\n            //console.log(typeof this.taskDueDate);\n        },\n        updateTask() {\n            var _this = this;\n            var id = this.taskId;\n            var title = this.fields.title;\n            var category_id = this.fields.category_id;\n            var taskPriority = this.fields.taskPriority;\n            var startDate = this.fields.startDate;\n            var completionDate = this.fields.completionDate;\n            var startTime = this.fields.startTime;\n            var completionTime = this.fields.completionTime;\n            var taskStatus = this.fields.taskStatus;\n            var assignable_id = this.fields.assignable_id;\n            var assignable_type = this.fields.assignable_type;\n            //console.log(id,title,category_id,member_id,taskPriority,dueDate,taskStatus);\n            //   axios({\n            //     method: \"post\",\n            //     url: \"/updatetask/\" + id,\n            //     data: {\n            //       title: title,\n            //       category_id: category_id,\n            //       member_id: member_id,\n            //       taskPriority: taskPriority,\n            //       dueDate: dueDate,\n            //       taskStatus: taskStatus,\n            //     },\n            //   })\n            //     .then(response => _this.getTasks())\n            //     .catch(error => console.log(error));\n            axios\n                .patch(\n                    \"/updatetask/\" + id,\n                    {\n                        title: title,\n                        category_id: category_id,\n                        member_id: member_id,\n                        taskPriority: taskPriority,\n                        startDate: startDate,\n                        completionDate: completionDate,\n                        startTime: startTime,\n                        completionTime: completionTime,\n                        taskStatus: taskStatus,\n                        assignable_id: assignable_id,\n                        assignable_type: assignable_type,\n                    },\n                    {\n                        headers: {\n                            \"Content-Type\": \"application/json\",\n                            // Authorization: 'Bearer ' + varToken\n                        },\n                    }\n                )\n                .then((response) => {\n                    _this.getTasks();\n                    _this.loading = false;\n                    _this.doEdit(false);\n                    //console.log($(\"#myModal button:nth-child(1)\"));\n                    // $(\"#myModal button:nth-child(1)\").attr(\n                    //     \"data-dismiss\",\n                    //     \"modal\"\n                    // );\n                    //$(\"#myModal button:nth-child(1)\").click();\n                    //console.log(\"ss\");\n                })\n                .catch((error) => console.log(error));\n        },\n        deleteTask(task) {\n            var _this = this;\n            axios\n                .post(\"/deletetask/\" + task.id)\n                .then(function (response) {\n                    _this.getTasks();\n                })\n                .catch((error) => {\n                    console.log(\"Delete task: \" + error);\n                });\n        },\n        closeForm() {\n            this.showDropdown = false;\n        },\n    },\n    created: function () {\n        window.addEventListener(\"click\", this.closeForm);\n    },\n    computed: {\n        reversedTasks() {\n            return [...this.tasks].reverse().sort();\n        },\n    },\n};\n</script>\n\n<style scoped>\n.counter {\n    font-size: 0.8rem;\n    padding-left: 10px;\n    padding-right: 10px;\n}\n\n#task-list {\n    padding: 1rem;\n    border-radius: 3px;\n    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08); */\n    width: 95%;\n    max-width: 100%;\n}\n\nh1 {\n    color: #3d4852;\n}\n\nul {\n    list-style: none;\n    padding: 0;\n}\n\na {\n    color: #6cb2eb;\n    font-size: 1.25rem;\n    transition: all 0.1s ease-in;\n    margin-top: 0.5rem;\n    display: block;\n}\n\na:hover {\n    color: #3490dc;\n}\n\nli,\np {\n    display: flex;\n    align-items: center;\n    letter-spacing: 0.5px;\n    color: #3d4852;\n    cursor: pointer;\n    transition: all 0.1s ease-in;\n}\n\nli:hover {\n    color: #22292f;\n}\n\nli input {\n    margin: 0 0.5rem 0;\n}\n\n#task-list > input,\n#task-list > select {\n    width: 100%;\n    border-radius: 3px;\n    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);\n    border: 1px solid #f1f5f8;\n    color: #606f7b;\n    padding: 0.5rem 0.75rem;\n    box-sizing: border-box;\n    font-size: 1rem;\n    letter-spacing: 0.5px;\n    margin: 0.5rem 0;\n}\n\n.btn {\n    border: none;\n    border-radius: 3px;\n    margin: auto 0;\n    padding: 0.5rem 0.75rem;\n    flex-shrink: 0;\n    cursor: pointer;\n    font-size: 0.9rem;\n    letter-spacing: 0.5px;\n    transition: all 0.1s ease-in;\n}\n\n.btn[disabled] {\n    background: #8795a1;\n}\n\n.btn[disabled]:hover {\n    background: #606f7b;\n}\n\n.btn-primary {\n    background: #6cb2eb;\n    color: #fff;\n}\n\n.btn-primary:hover {\n    background: #3490dc;\n}\n\n.btn-cancel {\n    background: #ef5753;\n    color: #000;\n}\n\n.btn-cancel:hover {\n    background: #e3342f;\n    color: rgba(255, 255, 255);\n}\n\n.strikeout {\n    text-decoration: line-through;\n    color: white;\n    background-color: lightblue;\n}\n\n.notstarted {\n    background-color: red;\n    color: white;\n    border-radius: 2em;\n    padding: 0.3em 0.5em;\n}\n\ntr {\n    margin: 0.3em 0;\n}\n\n.inprogress {\n    background-color: yellow;\n    color: white;\n}\n\n.completed {\n    background-color: green;\n    color: white;\n}\n\n.strikeout:hover {\n    color: #8795a1;\n}\n\n.priority {\n    color: #de751f;\n}\n\ninput:hover,\nselect:hover {\n    background-color: pink;\n    cursor: pointer;\n}\n\ninput[type=\"checkbox\"] {\n    display: inline !important;\n    box-shadow: none;\n}\n\ntd:hover {\n    cursor: pointer;\n}\n.task-title {\n    font-family: \"Perpetua\";\n    font-style: normal;\n    font-weight: 700;\n    font-size: 24.4444px;\n    line-height: 28px;\n    text-align: center;\n    position: relative;\n    color: #333333;\n}\n.task-title > button {\n    background: #c4456f;\n    border-radius: 5px;\n    padding: 11px 18px 12px;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 11px;\n    line-height: 16px;\n    color: #ffffff;\n    position: absolute;\n    right: 0;\n    outline: none;\n    border: none;\n    display: flex;\n    align-items: center;\n    top: 20px;\n}\n.task-title > button > span {\n    font-size: 17px;\n    margin-right: 5px;\n}\n.form-group-custom > label {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 800;\n    font-size: 12px;\n    line-height: 8px;\n    margin-left: 10px;\n\n    color: #000000;\n}\n.form-group-custom > input,\n.form-group-custom > select {\n    background: rgba(239, 220, 226, 0.2);\n    border: 0.643349px solid #f7dae4;\n    border-radius: 7.72019px;\n    margin-bottom: 12px;\n}\n.form-group-custom > select {\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    background-image: url(/assets/icon-down.svg);\n    background-repeat: no-repeat;\n    background-position: 98%;\n    background-size: 20px;\n}\n.form-group-custom > input::placeholder {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 10px;\n    line-height: 8px;\n    margin-left: 10px;\n\n    color: #000000;\n}\n.form-group-custom > button {\n    background: #c4456f;\n    border-radius: 7.72px;\n    width: 100%;\n    display: inline-block;\n    padding: 11px;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 9.10182px;\n    line-height: 14px;\n    /* identical to box height */\n\n    text-align: center;\n    box-shadow: none !important;\n    color: #ffffff;\n}\n.form-row {\n    width: 50%;\n    margin: auto;\n}\n.form-label-radio > input {\n    margin-right: 5px;\n}\n.date-grp {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: space-between;\n}\n.date-grp > div {\n    width: 48%;\n}\n.date-grp > div > input {\n    width: 100%;\n}\n.task-list-single {\n    background: #ffffff;\n    border: 0.5px solid #d2d2d2;\n    border-radius: 5px;\n    padding: 22px;\n    margin-bottom: 10px;\n}\n.single-first-row {\n    display: flex;\n    justify-content: space-between;\n    align-items: flex-start;\n    margin-bottom: 12px;\n}\n.single-first-row > div:nth-child(1) {\n    display: flex;\n    flex-wrap: wrap;\n}\n.single-first-row > div:nth-child(2) {\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n}\n.single-first-row > div:nth-child(1) > span:nth-child(1) {\n    background: #f31c45;\n    border-radius: 10px;\n    padding: 5px 8px;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12.74194px;\n    line-height: 12px;\n    color: #ffffff;\n    margin-right: 10px;\n    margin-bottom: 5px;\n    display: inline-block;\n    text-align: center;\n}\n.single-first-row > div:nth-child(1) > span:nth-child(2) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 300;\n    font-size: 14px;\n    line-height: 18px;\n    color: #000000;\n}\n.single-first-row > div:nth-child(2) > span:nth-child(1) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12.60469px;\n    line-height: 13px;\n    margin-right: 10px;\n    color: #2fa706;\n}\n.single-first-row > div:nth-child(2) > span:nth-child(2) {\n    display: flex;\n}\n.single-first-row > div:nth-child(2) > span:nth-child(1) > img {\n    width: 17px;\n    height: 12px;\n}\n.single-first-row > div:nth-child(2) > span:nth-child(2) > img {\n    width: 37px;\n    height: 32px;\n    margin-right: 5px;\n    cursor: pointer;\n}\n.single-second-row {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 12px;\n    line-height: 15px;\n    color: #000000;\n    margin-bottom: 17px;\n}\n.single-third-row {\n    display: flex;\n    align-items: center;\n}\n.single-third-row > div {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 300;\n    font-size: 10.60469px;\n    line-height: 13px;\n    color: #000000;\n    margin-right: 15px;\n    display: flex;\n    align-items: center;\n}\n.single-third-row > div > img {\n    margin-right: 5px;\n}\n.single-third-row > div:nth-child(3) > span {\n    width: 7px;\n    height: 7px;\n    display: inline-block;\n    border-radius: 50%;\n    background-color: #2fa706;\n    margin-right: 5px;\n}\n.single-third-row > div:nth-child(3) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 9.99808px;\n    line-height: 12px;\n    /* identical to box height */\n\n    color: #2fa706;\n}\n.custom-select-box {\n    min-height: 37px;\n    position: relative;\n}\n.custom-select-box > span {\n    background-image: url(/assets/icon-down.svg);\n    background-repeat: no-repeat;\n    background-position: 98%;\n    background-size: 20px;\n    background-color: rgba(239, 220, 226, 0.2);\n    border: 0.643349px solid #f7dae4;\n    border-radius: 7.72019px;\n    cursor: pointer;\n    display: block;\n    width: 100%;\n    padding: 0.375rem 0.75rem;\n    font-size: 0.9rem;\n    font-weight: 400;\n    line-height: 1.6;\n    color: #212529;\n    height: 37px;\n}\n.custom-select-box > span:hover {\n    background-color: pink !important;\n}\n.custom-select-box > div > span {\n    background-color: rgba(239, 220, 226, 0.2);\n}\n.custom-select-box > div {\n    background-color: pink;\n    border: 0.643349px solid #f7dae4;\n    border-radius: 7.72019px;\n    margin-bottom: 12px;\n    cursor: pointer;\n    display: block;\n    width: 100%;\n    padding: 0.375rem 0.75rem;\n    font-size: 0.9rem;\n    font-weight: 400;\n    line-height: 1.6;\n    color: #212529;\n    position: absolute;\n    top: 36px;\n    left: 0;\n}\n.custom-select-box > div > span {\n    display: block;\n    width: 100%;\n    padding: 5px;\n}\n@media only screen and (max-width: 768px) {\n    .single-third-row {\n        flex-wrap: wrap;\n    }\n    .single-third-row > div:nth-child(3) {\n        width: 100%;\n        margin-top: 7px;\n    }\n    .task-title > button {\n        position: unset;\n        display: block;\n        margin: 5px auto;\n    }\n}\n@media only screen and (max-width: 996px) {\n    .form-row {\n        width: 100%;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
